* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background: linear-gradient(315deg, #65005e 3%, #6c0606 38%, #270be0 68%, #e8940c 98%) 0 0 / 400% 400% fixed;
  margin: 16px;
  padding-top: 10px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  animation: gradient 15s infinite;
  overflow: auto;
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 100%;
  }

  100% {
    background-position: 0 0;
  }
}

.wave {
  width: 200%;
  height: 12em;
  opacity: .8;
  z-index: -1;
  background: rgba(255, 255, 255, .25);
  border-radius: 1000% 1000% 0 0;
  animation: wave 10s linear -3s infinite;
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  opacity: .8;
  animation: wave 18s linear infinite reverse;
  bottom: -1.25em;
}

.wave:nth-of-type(3) {
  opacity: .9;
  animation: wave 20s -1s infinite reverse;
  bottom: -2.5em;
}

@keyframes wave {
  2% {
    transform: translateX(1px);
  }

  25% {
    transform: translateX(-25%);
  }

  50% {
    transform: translateX(-50%);
  }

  75% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(1px);
  }
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

ul {
  list-style: none;
}

.main-item {
  margin-bottom: 50px;
}

.custom-btn {
  width: 50%;
  color: #fff;
  cursor: pointer;
  background: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.main-link {
  background: none;
  border: none;
  padding: 0;
  line-height: 42px;
}

.main-link span {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.main-link :before, .main-link :after {
  content: "";
  height: 0%;
  width: 1px;
  background: none;
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.main-link :before {
  transition: all .5s;
  top: 0;
  right: 0;
}

.main-link :after {
  transition: all .5s;
  bottom: 0;
  left: 0;
}

.main-link :hover {
  color: #76aef1;
  box-shadow: none;
  background: none;
}

.main-link :hover:before, .รง :hover:after {
  height: 100%;
  transition: all .5s;
}

.main-link span:before, .main-link span:after {
  content: "";
  background: none;
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.main-link span:before {
  width: 0%;
  height: .5px;
  transition: all .5s;
  top: 0;
  left: 0;
}

.main-link span:after {
  width: 0%;
  height: .5px;
  transition: all .5s;
  bottom: 0;
  right: 0;
}

.main-link span:hover:before, .main-link span:hover:after {
  width: 100%;
}

